import axios from 'axios';
import { AlertFeedback } from '../feedback/alert-feedback';

export enum MailConfigType {
  'GOOGLE' = 'GOOGLE',
}

export type MailConfiguration = {
  id: number;
  type: MailConfigType;
  lastSync: string;
  updatedAt: string;
  createdAt: string;
  mailsCount: number;
};

/**
 * Load tokens for google connecion. Will be called as second step of google connection
 */
async function GetGoogleApiCallback(code: string): Promise<any | null> {
  const result = await axios.get('mail/google/callback', { params: { code } });
  return result?.data?.response;
}

async function GetGmailSetupUrl() {
  try {
    const result = await axios.get<{ url: string; reference: string }>('/mail/google/url');
    const consentWindow = window.open(result.data.url, '_blank', 'width=600,height=600');
    if (consentWindow) {
      consentWindow.focus();
    } else {
      throw new Error();
    }
  } catch (error) {
    AlertFeedback.error.defaultError();
  }
}

async function SyncGoogleMails(): Promise<MailConfiguration | undefined> {
  try {
    const result = await axios.post<{ reference: string; mailConfiguration: MailConfiguration }>(
      '/mail/google/sync'
    );
    return result.data.mailConfiguration;
  } catch (error) {
    AlertFeedback.error.defaultError();
  }
}

type MailApiType = {
  GetGoogleApiCallback: typeof GetGoogleApiCallback;
  GetGmailSetupUrl: typeof GetGmailSetupUrl;
  SyncGoogleMails: typeof SyncGoogleMails;
};

const MailApi: MailApiType = {
  GetGoogleApiCallback,
  GetGmailSetupUrl,
  SyncGoogleMails,
};

export default MailApi;
