import { Button, Typography } from '@mui/material';
import React from 'react';
import './package-content.component.css';
import CheckIcon from '@mui/icons-material/Check';
import CloseIcon from '@mui/icons-material/Close';

type PackageContentProps = {
  name: string;
  cost: number;
  featureMap: boolean[];
  onSelect: () => void;
};

export default function PackageContent(props: PackageContentProps) {
  const costString = (props.cost / 100)
    .toFixed(2)
    .toString()
    .replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,');

  const enabledClassName = 'PackageContent-Features --enabled';
  const disabledClassName = 'PackageContent-Features --disabled';

  const getClassName = (index) => {
    if (props.featureMap[index]) return enabledClassName;
    return disabledClassName;
  };

  const Icon = ({ index }: { index: number }) => {
    if (props.featureMap[index]) return <CheckIcon />;
    return <CloseIcon />;
  };

  return (
    <>
      <Typography variant="h5" mb={1}>
        {props.name}
      </Typography>
      <p className="PackageContent-Pricing">
        <Typography variant="h4" mb={0}>
          {costString} 
        </Typography>
        <Typography variant="body1" className="PackageContent-PricingMonth">
          / Monat
        </Typography>
      </p>

      <hr className="PackageContent-hr" />

      <div className="PackageContent-Features">
        <Typography variant="body2" className={getClassName(0)}>
          <Icon index={0} /> Unbegrenzte Anzahl an Datenstzen
        </Typography>

        <Typography variant="body2" className={getClassName(1)}>
          <Icon index={1} /> Individuelle Auswahl von Modulen
        </Typography>

        <Typography variant="body2" className={getClassName(2)}>
          <Icon index={2} /> Simple und schnelle Web-Oberflche
        </Typography>

        <Typography variant="body2" className={getClassName(3)}>
          <Icon index={3} /> Kein Schnick-Schnack
        </Typography>

        <Typography variant="body2" className={getClassName(4)}>
          <Icon index={4} /> Direkte Telefonleitung zu Marko
        </Typography>

        <Typography variant="body2" className={getClassName(5)}>
          <Icon index={5} /> Kostenloser Zugriff auf alle Module
        </Typography>
      </div>

      <Button variant="contained" size="large" fullWidth onClick={props.onSelect}>
        {props.name} auswhlen
      </Button>
    </>
  );
}
