import SearchIcon from '@mui/icons-material/Search';
import { InputAdornment } from '@mui/material';
import Autocomplete from '@mui/material/Autocomplete';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import axios from 'axios';
import { AlertFeedback } from 'modules/shared/feedback/alert-feedback';
import React, { useEffect } from 'react';
import { useDebounce } from 'use-debounce';

export type AutoCompleteOption = {
  title: string;
  text: string;
  id: string;
};

type ServerSideAutocompleteProps = {
  id: string;
  label: string;
  placeholder: string;
  noOptionsText: string;
  url: string;
};

async function loadData(url, searchQuery) {
  const result = await axios.get<{ response: AutoCompleteOption[]; reference: string }>(url, {
    params: { query: searchQuery },
  });
  return result.data.response;
}

export default function ServerSideAutocomplete(props: ServerSideAutocompleteProps) {
  const [value, setValue] = React.useState<AutoCompleteOption | null>(null);
  const [inputValue, setInputValue] = React.useState('');
  const [options, setOptions] = React.useState<readonly AutoCompleteOption[]>([]);

  const [debouncedValue] = useDebounce(inputValue, 250);

  useEffect(() => {
    async function loader() {
      try {
        const result = await loadData(props.url, debouncedValue);
        setOptions(result);
      } catch (error) {
        AlertFeedback.error.defaultError();
      }
    }
    loader();
  }, [debouncedValue]);

  return (
    <Autocomplete
      id={props.id}
      getOptionLabel={(option) => (typeof option === 'string' ? option : option.title)}
      filterOptions={(x) => x}
      options={options}
      autoComplete
      includeInputInList
      value={value}
      noOptionsText={props.noOptionsText}
      onChange={(event: any, newValue: AutoCompleteOption | null) => {
        setValue(newValue);
      }}
      onInputChange={(event, newInputValue) => {
        setInputValue(newInputValue);
      }}
      isOptionEqualToValue={(testOption: AutoCompleteOption, testValue: AutoCompleteOption) =>
        testOption.id === testValue.id
      }
      renderInput={(params) => (
        <TextField
          {...params}
          label={props.label}
          fullWidth
          InputProps={{
            ...params.InputProps,
            placeholder: props.placeholder,
            startAdornment: (
              <InputAdornment
                sx={{
                  marginRight: '0',
                }}
                position="start"
              >
                <SearchIcon />
              </InputAdornment>
            ),
          }}
        />
      )}
      renderOption={(renderProps, option) => (
        <li {...renderProps} key={option.id}>
          <Box display="flex" flexDirection="column">
            <Typography variant="body2" fontWeight="bold">
              {option.title}
            </Typography>
            <Typography variant="caption">{option.text}</Typography>
          </Box>
        </li>
      )}
    />
  );
}
