import { Box, Card, Container, ToggleButton, ToggleButtonGroup, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import './packages.page.css';
import Grid2 from '@mui/material/Unstable_Grid2/Grid2';
import { useNavigate } from 'react-router';
import PackageContent from './components/package-content.component';

export enum Cadence {
  'monthly',
  'yearly',
}
export enum Package {
  'free',
  'essential',
  'premium',
}

export default function PackagesPage() {
  const { t } = useTranslation();
  const [cadence, setCadence] = useState(Cadence.yearly);
  const navigate = useNavigate();

  useEffect(() => {
    window.scroll(0, 0);
  }, []);

  const handlCadenceChange = (event: React.MouseEvent<HTMLElement>, newCadence: Cadence) => {
    setCadence(newCadence);
  };

  const onSelect = (packageType) => {
    navigate(`payment?package=${packageType}&cadence=${cadence}`);
  };

  return (
    <>
      <Box my="1em">
        <Typography variant="h3" component="h1" textAlign="center">
          Willkommen im MicroCRM
        </Typography>
      </Box>
      <Box my="1em">
        <Typography variant="body1" component="h4" textAlign="center" mb={3}>
          Bitte whlen Sie ein passendes Paket und Modell.
        </Typography>
      </Box>

      <ToggleButtonGroup
        value={cadence}
        exclusive
        onChange={handlCadenceChange}
        aria-label="cadence selection"
        className="CadenceToggle"
      >
        <ToggleButton value={Cadence.yearly} aria-label="yearly">
          Jahres-Paket
        </ToggleButton>
        <div className="CadenceSale">
          <Typography variant="subtitle2">Spare 10%</Typography>
        </div>
        <ToggleButton value={Cadence.monthly} aria-label="monthly">
          Monats-Paket
        </ToggleButton>
      </ToggleButtonGroup>

      <Grid2 container rowSpacing={4} columnSpacing={2}>
        <Grid2 xs={12} md={4} display="flex" justifyContent="center">
          <Card elevation={0} className="PackageCard">
            <PackageContent
              name="Free"
              cost={0}
              featureMap={[true, true, false, false, false, false]}
              onSelect={() => onSelect(Package.free)}
            />
          </Card>
        </Grid2>
        <Grid2 xs={12} md={4} display="flex" justifyContent="center">
          <Card elevation={24} className="PackageCard --bestSeller">
            <div className="PackageCard-Bestseller">Bestseller</div>
            <PackageContent
              name="Essential"
              cost={cadence === Cadence.monthly ? 1199 + 0.1 * 1199 : 1199}
              featureMap={[true, true, true, true, false, false]}
              onSelect={() => onSelect(Package.essential)}
            />
          </Card>
        </Grid2>
        <Grid2 xs={12} md={4} display="flex" justifyContent="center">
          <Card elevation={0} className="PackageCard">
            <PackageContent
              name="Premium"
              cost={cadence === Cadence.monthly ? 1999 + 0.1 * 1999 : 1999}
              featureMap={[true, true, true, true, true, true]}
              onSelect={() => onSelect(Package.premium)}
            />
          </Card>
        </Grid2>
      </Grid2>
    </>
  );
}
