import CreditCards from 'assets/credit-cards';
import React, { useEffect, useMemo, useState } from 'react';
import './credit-card.component.css';
import {
  Alert,
  AlertTitle,
  Button,
  CardActionArea,
  CardActions,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  IconButton,
  Skeleton,
  Typography,
  useMediaQuery,
  useTheme,
} from '@mui/material';
import SettingsIcon from '@mui/icons-material/Settings';
import AddCardIcon from '@mui/icons-material/AddCard';
import { useTranslation } from 'react-i18next';
import { AlertFeedback } from 'modules/shared/feedback/alert-feedback';
import SettingsApi from 'modules/shared/api/settings.api';

export enum CreditCardVariant {
  'Visa',
  'Mastercard',
  'American Express',
  'Discover',
  'Diners Club',
  'JCB',
}

// move to BE
const cardTypes = [
  {
    type: 'Visa',
    pattern: /^4/,
    lengths: [13, 16, 19],
  },
  {
    type: 'Mastercard',
    pattern: /^5[1-5]/,
    lengths: [16],
  },
  {
    type: 'American Express',
    pattern: /^3[47]/,
    lengths: [15],
  },
  {
    type: 'Discover',
    pattern: /^(6011|65|64[4-9]|622)/,
    lengths: [16, 19],
  },
  {
    type: 'Diners Club',
    pattern: /^(36|38|30[0-5])/,
    lengths: [14],
  },
  {
    type: 'JCB',
    pattern: /^35/,
    lengths: [16, 17, 18, 19],
  },
];

export type CreditCardParameters = {
  number: number;
  variant: CreditCardVariant;
  expiryMonth: number;
  expiryYear: number;
  cvv: number;
};

/**
 * Component to show a credit card.
 * Includes button to open edit modal
 * @param props
 * @returns
 */
export default function CreditCard() {
  const { t } = useTranslation();
  const [loading, setLoading] = useState(true);
  const [cardData, setCardData] = useState<CreditCardParameters | Record<string, never>>({});
  const [dialogOpen, setDialogOpen] = useState(false);

  const CardIcon = CreditCards[`${cardData.variant || CreditCardVariant.Mastercard}`];

  const isCardFilled = useMemo(() => !loading && !!cardData.number, [loading, cardData.number]);

  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('md'));

  useEffect(() => {
    async function loadCreditCardData() {
      try {
        const data = await SettingsApi.LoadCreditCard();
        setCardData(data || {});
      } catch (error) {
        AlertFeedback.error.defaultError();
      }
      setLoading(false);
    }
    loadCreditCardData();
  }, []);

  if (!loading && !isCardFilled)
    return (
      <div className="CreditCard-Add">
        <Alert severity="info" className="CreditCard-AddHint">
          {t('credit_card_requested_hint')}
        </Alert>
        <Button variant="outlined" startIcon={<AddCardIcon />} onClick={() => setDialogOpen(true)}>
          {t('add_credit_card')}
        </Button>
        <Dialog fullScreen={fullScreen} open={dialogOpen}>
          <DialogTitle id="responsive-dialog-title">{"Use Google's location service?"}</DialogTitle>
          <DialogContent>
            <DialogContentText>
              Let Google help apps determine location. This means sending anonymous location data to
              Google, even when no apps are running.
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => setDialogOpen(false)}>Disagree</Button>
            <Button onClick={() => setDialogOpen(false)}>Agree</Button>
          </DialogActions>
        </Dialog>
      </div>
    );

  return (
    <div className="CreditCard">
      <div className="CreditCard-Head">
        {loading ? (
          <Skeleton>
            <div className="CreditCard-Icon" />
          </Skeleton>
        ) : (
          <CardIcon className="CreditCard-Icon" />
        )}

        <IconButton aria-label="edit" color="default" edge="end" size="small" disabled={loading}>
          <SettingsIcon />
        </IconButton>
      </div>
      <Typography variant={'subtitle2'}>{loading ? <Skeleton /> : cardData.number}</Typography>
    </div>
  );
}
