import Grid2 from '@mui/material/Unstable_Grid2/Grid2';
import { Form, Formik } from 'formik';

import FormCountrySelect from 'modules/shared/components/form-country-select/form-country-select.component';
import FormSubmitLoadingButton from 'modules/shared/components/form-loading-button/form-loading-button.component';
import FormTextField from 'modules/shared/components/form-text-field/form-text-field.component';
import { AlertFeedback } from 'modules/shared/feedback/alert-feedback';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import SettingsApi from 'modules/shared/api/settings.api';
import validateAddressForm from '../helper/validate-address-form/validate-address-form.helper';
import './billing-address-form.component.css';

export type BillingAddress = {
  street: string;
  house: string;
  zipCode: string;
  city: string;
  country: string;
  firstname: string;
  lastname: string;
  phone: string;
};

export default function BillingAddressForm() {
  const { t } = useTranslation();
  const [savedBilling, setSavedBilling] = useState<BillingAddress | null>(null);

  useEffect(() => {
    async function loadBilling() {
      try {
        const billing = await SettingsApi.LoadBillingAddress();
        setSavedBilling(billing);
      } catch (error) {
        AlertFeedback.error.defaultError();
      }
    }
    loadBilling();
  }, []);

  return (
    <Formik<BillingAddress>
      enableReinitialize
      initialValues={
        savedBilling || {
          street: '',
          house: '',
          zipCode: '',
          city: '',
          country: '',
          firstname: '',
          lastname: '',
          phone: '',
        }
      }
      onSubmit={async (values) => {
        try {
          await SettingsApi.UpdateBillingAddress(values);
          AlertFeedback.success.saved();
        } catch (error) {
          AlertFeedback.error.defaultError();
        }
      }}
      validate={validateAddressForm}
    >
      <Form>
        <Grid2 container spacing={3} rowSpacing={2}>
          <Grid2 xs={12} sm={6}>
            <FormTextField
              id="firstname"
              name="firstname"
              label={t('firstname')}
              fullWidth
              required
            />
          </Grid2>
          <Grid2 xs={12} sm={6}>
            <FormTextField id="lastname" name="lastname" label={t('lastname')} fullWidth required />
          </Grid2>
          <Grid2 xs={12} sm={6}>
            <FormTextField id="street" name="street" label={t('street')} fullWidth required />
          </Grid2>
          <Grid2 xs={12} sm={6}>
            <FormTextField id="house" name="house" label={t('house')} fullWidth required />
          </Grid2>
          <Grid2 xs={12} sm={6}>
            <FormTextField id="zipCode" name="zipCode" label={t('zipCode')} fullWidth required />
          </Grid2>
          <Grid2 xs={12} sm={6}>
            <FormTextField id="city" name="city" label={t('city')} fullWidth required />
          </Grid2>
          <Grid2 xs={12} sm={6}>
            <FormCountrySelect name="country" label={t('country')} fullWidth required />
          </Grid2>
          <Grid2 xs={12} sm={6}>
            <FormTextField id="phone" name="phone" label={t('phone')} fullWidth required />
          </Grid2>
        </Grid2>

        <div className="BillingAddress-Action">
          <FormSubmitLoadingButton size="medium">{t('Save changes')}</FormSubmitLoadingButton>
        </div>
      </Form>
    </Formik>
  );
}
