import { Box, Card, CardHeader, Typography } from '@mui/material';
import { Form, Formik } from 'formik';
import AuthenticationApi from 'modules/authentication/api/authentication.api';
import AuthenticationContext from 'modules/authentication/state/authentication.context';
import FormPasswordInput from 'modules/shared/components/form-password-input/form-password-input.component';
import FormSubmitButton from 'modules/shared/components/form-submit-button/form-submit-button.component';
import FormTextField from 'modules/shared/components/form-text-field/form-text-field.component';
import React, { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { Link, useSearchParams } from 'react-router-dom';
import validateForm from './helper/validate-form/validate-form.helper';
import './login.page.css';

export type FormValues = {
  password: string;
  email: string;
};

export default function LoginPage() {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const paramsEmail = searchParams.get('email');
  const { onLogin } = useContext(AuthenticationContext);

  return (
    <>
      <Box my="1em">
        <Typography variant="h3" component="h1" textAlign="center">
          {t('sign_in_title')}
        </Typography>
      </Box>
      <Box my="1em">
        <Typography variant="body1" component="h4" textAlign="center" mb={3}>
          {t('login_to_your_account')}
        </Typography>
      </Box>

      <Box display="flex" justifyContent="center">
        <Card className="FormCard">
          <CardHeader title={t('account_data')} />
          <Formik<FormValues>
            initialValues={{
              password: '',
              email: paramsEmail || '',
            }}
            onSubmit={async (values) => {
              const response = await AuthenticationApi.Login(values.email, values.password);
              if (response) {
                onLogin(response.data);
              }
            }}
            validate={validateForm}
            validateOnBlur
            validateOnChange={false}
          >
            <Form>
              <FormTextField
                id="email"
                name="email"
                label={t('email')}
                type="email"
                fullWidth
                focus={!paramsEmail}
              />
              <FormPasswordInput
                name="password"
                label={t('password')}
                id="password"
                autoComplete
                focus={!!paramsEmail}
              />
              <div className="Login-ForgotLink">
                <Link to="/forgot">{t('forgot_password')}</Link>
              </div>

              <div className="Login-ActionContainer">
                <FormSubmitButton label={t('login')} />
              </div>
            </Form>
          </Formik>
        </Card>
      </Box>
    </>
  );
}
