import { useEffect, useState } from 'react';
import User from 'modules/shared/user/types/user.type';
import Authentication from '../types/authentication.type';
import AuthenticationApi from '../api/authentication.api';

export default function useAuthentication(): Authentication & { user?: User } {
  const [email, setEmail] = useState<string | undefined>(undefined);
  const [apiKey, setApiKey] = useState<string | undefined>(undefined);
  const [user, setUser] = useState<User | undefined>();
  const [validating, setValidating] = useState(true);

  useEffect(() => {
    async function tryToValidateOnReload() {
      try {
        const result = await AuthenticationApi.Validate();
        if (result?.data) {
          setUser(result.data.response);
        }
      } catch {
        setUser(undefined);
        setApiKey(undefined);
        setEmail(undefined);
      }
      setValidating(false);
    }
    tryToValidateOnReload();
  }, []);

  return {
    email,
    apiKey,
    user,
    validating,
    onLogin: (loginObject) => {
      setEmail(loginObject.email);
      setUser(loginObject);
      setApiKey(loginObject.apikey);
    },
    onLogout: () => {
      setEmail(undefined);
      setUser(undefined);
      setApiKey(undefined);
    },
  };
}
