import { Card, CardHeader, Container, Typography } from '@mui/material';
import CaseApi, { CaseStatus } from 'modules/shared/api/case.api';
import Loading from 'modules/shared/components/loading/loading-spinner.component';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { AlertFeedback } from 'modules/shared/feedback/alert-feedback';
import CaseStatusTable from './components/case-status-table.component';
import CaseStatusEditModal from './components/case-status-edit-modal.component';

export default function CaseStatusPage() {
  const { t } = useTranslation();
  const [statuses, setStatuses] = useState<CaseStatus[]>([]);
  const [loading, setLoading] = useState(true);
  const [loadingAction, setLoadingAction] = useState(false);

  const onDelete = async (statusId: number) => {
    try {
      setLoadingAction(true);
      await CaseApi.DeleteCaseStatus(statusId);
      const newStatuses = statuses.filter(({ id }) => id !== statusId);
      setStatuses(newStatuses);
    } catch (error) {
      AlertFeedback.error.defaultError();
    }
    setLoadingAction(false);
  };

  const onEditSave = async (statusToSave: CaseStatus) => {
    console.log('onEditSave');
    try {
      setLoadingAction(true);
      const editedStatus = await CaseApi.EditCaseStatus(statusToSave);
      const newStatuses: CaseStatus[] = statuses.map((status) =>
        editedStatus.id === status.id ? editedStatus : status
      );
      setStatuses(newStatuses);
    } catch (error) {
      AlertFeedback.error.defaultError();
    }
    setLoadingAction(false);
  };

  const onCreateStatus = async (color: string, label: string) => {
    try {
      setLoadingAction(true);
      const status = await CaseApi.AddCaseStatus({ color, label });
      const newStatuses: CaseStatus[] = [...statuses, status];
      setStatuses(newStatuses);
    } catch (error) {
      AlertFeedback.error.defaultError();
    }
    setLoadingAction(false);
  };

  const [statusToEdit, setStatusToEdit] = useState<CaseStatus>();
  const onEdit = (statusId: number) => setStatusToEdit(statuses.find(({ id }) => id === statusId));

  const loadStatuses = async () => {
    setLoading(true);
    const result = await CaseApi.GetCaseStatuses();
    setStatuses(result);
    setLoading(false);
  };

  useEffect(() => {
    loadStatuses();
  }, []);

  return (
    <Container>
      <Typography variant="h4" component="h1" sx={{ marginBottom: '2rem' }}>
        {t('case_statuses_title')}
      </Typography>
      <Typography variant="body2" component="p" sx={{ marginBottom: '2rem' }}>
        {t('case_statuses_subtitle')}
      </Typography>
      {loading && <Loading loading />}

      {statuses && !loading && (
        <Card>
          <CardHeader title={t('current_settings')} />
          <CaseStatusTable statuses={statuses} onDelete={onDelete} onEdit={onEdit} />
          <CaseStatusEditModal
            status={statusToEdit}
            handleClose={() => setStatusToEdit(undefined)}
            handleSave={onEditSave}
          />
        </Card>
      )}
    </Container>
  );
}
