import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Box,
  Button,
  Card,
  CardActions,
  CardContent,
  CardHeader,
  Link,
  Typography,
} from '@mui/material';
import Grid2 from '@mui/material/Unstable_Grid2/Grid2';
import CreditCards from 'assets/credit-cards';
import PayPalLogo from 'assets/paypal.svg';
import { Form, Formik } from 'formik';
import AuthenticationApi from 'modules/authentication/api/authentication.api';
import PaymentApi, { CostPreviewData } from 'modules/shared/api/payment.api';
import FormCheckbox from 'modules/shared/components/form-checkbox/form-checkbox.component';
import FormPasswordInput from 'modules/shared/components/form-password-input/form-password-input.component';
import FormTextField from 'modules/shared/components/form-text-field/form-text-field.component';
import { AlertFeedback } from 'modules/shared/feedback/alert-feedback';
import React, { useEffect, useMemo, useState } from 'react';
import { Trans, useTranslation } from 'react-i18next';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { Cadence, Package } from '../packages/packages.page';
import OrderSummary from './component/order-summary.component';
import './payment.page.css';
import { LoadingButton } from '@mui/lab';
import FormSubmitLoadingButton from 'modules/shared/components/form-loading-button/form-loading-button.component';

type FormValues = {
  email: string;
  password: string;
  passwordRepeat: string;
  isCorporate: boolean;
  firstname: string;
  lastname: string;
  cardNumber: string;
  expirationDate: string;
  cvv: string;
  zipCode: string;
};

export default function PaymentPage() {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const [accordionExpanded, setAccordionExpanded] = React.useState<string | false>(false);
  const handleChangeAccordionState =
    (panel: string) => (event: React.SyntheticEvent, isExpanded: boolean) => {
      setAccordionExpanded(isExpanded ? panel : false);
    };

  useEffect(() => {
    window.scroll(0, 0);
  }, []);

  const validateForm = () => {};
  const [searchParams] = useSearchParams();
  const [selectedPackage] = useState<Package>(() =>
    Object.values(Package).includes(parseInt(searchParams.get('package') || '0', 10))
      ? (searchParams.get('package') as unknown as Package)
      : Package.free
  );
  const [cadence] = useState<Cadence>(() =>
    Object.values(Cadence).includes(parseInt(searchParams.get('cadence') || '0', 10))
      ? (searchParams.get('cadence') as unknown as Cadence)
      : Cadence.monthly
  );

  const [previewLoading, setPreviewLoading] = useState(true);
  const [previewData, setPreviewData] = useState<CostPreviewData>();

  useEffect(() => {
    async function loadPreview() {
      try {
        const result = await PaymentApi.LoadCostPreview(selectedPackage, cadence, 'germany');
        if (result.data.response) {
          setPreviewData(result.data.response);
        }
      } catch (error) {
        AlertFeedback.error.defaultError();
      }
      setPreviewLoading(false);
    }

    loadPreview();
  }, []);

  const cards = useMemo(() => [CreditCards[0], CreditCards[1], CreditCards[2], CreditCards[3]], []);

  return (
    <>
      <Box my="1em">
        <Typography variant="h3" component="h1" textAlign="center">
          {t('create_an_account')}
        </Typography>
      </Box>
      <Box my="1em">
        <Typography variant="body1" component="h4" textAlign="center" mb={3}>
          {t('fill_out_to_get_started')}
        </Typography>
      </Box>
      <Formik<FormValues>
        initialValues={{
          password: '123456',
          passwordRepeat: '123456',
          email: 'test123@test.de',
          isCorporate: false,
          firstname: 'Tobi',
          lastname: 'test',
          cardNumber: '4242 4242 4242 4242',
          expirationDate: '12/23',
          cvv: '123',
          zipCode: '50999',
        }}
        onSubmit={async (values) => {
          const email = await AuthenticationApi.Register(
            values.email,
            values.password,
            values.isCorporate,
            selectedPackage,
            cadence,
            {
              firstname: values.firstname,
              lastname: values.lastname,
              cardNumber: values.cardNumber,
              expirationDate: values.expirationDate,
              cvv: values.cvv,
              zipCode: values.zipCode,
            }
          );
          if (email) {
            navigate(`/?email=${email}`);
          }
        }}
        validate={validateForm}
      >
        <Form>
          <Grid2 container spacing={4} rowSpacing={4}>
            <Grid2 xs={12} md={7} className="Grid-growChildren">
              <div>
                <Grid2 container rowSpacing={4}>
                  <Grid2 xs={12}>
                    <Card>
                      <CardHeader title={t('account_data')} />
                      <CardContent>
                        <FormTextField
                          id="email"
                          name="email"
                          label={t('email')}
                          type="email"
                          fullWidth
                        />
                        <FormPasswordInput
                          name="password"
                          label={t('password')}
                          id="password"
                          autoComplete
                        />
                        <FormPasswordInput
                          name="passwordRepeat"
                          label={t('password_repeat')}
                          id="passwordRepeat"
                        />
                        <FormCheckbox
                          className="Payment-Checkbox"
                          name="isCorporate"
                          label={t('company_account')}
                        />
                      </CardContent>
                      <CardActions>
                        <Typography variant="body2">
                          <Trans
                            i18nKey="terms_of_service_hint"
                            components={{
                              linkComponent1: <Link href="#" />,
                              linkComponent2: <Link href="#" />,
                            }}
                          />
                        </Typography>
                      </CardActions>
                    </Card>
                  </Grid2>
                  <Grid2 xs={12}>
                    <Card>
                      <CardHeader title={t('payment_method')} />
                      <CardContent>
                        <Accordion
                          disableGutters
                          className="borderVariant"
                          expanded={accordionExpanded === 'panel1'}
                          onChange={handleChangeAccordionState('panel1')}
                          square
                        >
                          <AccordionSummary
                            className="borderVariant Payment-AccordionSummary"
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls="panel1a-content"
                            id="panel1a-header"
                          >
                            <Typography className="MuiAccordionSummary-Typography">
                              {t('credit_or_debit_card')}
                            </Typography>
                            {cards.map((CardIcon, index) => (
                              <div className="Payment-CreditIcon" key={index}>
                                <CardIcon />
                              </div>
                            ))}
                          </AccordionSummary>
                          <AccordionDetails className="borderVariant Payment-AccordionDetails">
                            <Grid2 container spacing={4} rowSpacing={0}>
                              <Grid2 xs={12} md={6}>
                                <FormTextField
                                  required
                                  id="firstname"
                                  name="firstname"
                                  label={t('firstname')}
                                  type="text"
                                  fullWidth
                                />
                              </Grid2>
                              <Grid2 xs={12} md={6}>
                                <FormTextField
                                  required
                                  id="lastname"
                                  name="lastname"
                                  label={t('lastname')}
                                  type="text"
                                  fullWidth
                                />
                              </Grid2>
                              <Grid2 xs={12} md={6}>
                                <FormTextField
                                  required
                                  id="cardNumber"
                                  name="cardNumber"
                                  label={t('card_number')}
                                  type="text"
                                  fullWidth
                                />
                              </Grid2>
                              <Grid2 xs={12} md={6}>
                                <FormTextField
                                  required
                                  id="expirationDate"
                                  name="expirationDate"
                                  label={t('expiration_date')}
                                  type="text"
                                  fullWidth
                                />
                              </Grid2>
                              <Grid2 xs={6}>
                                <FormTextField
                                  required
                                  id="cvv"
                                  name="cvv"
                                  label={t('cvv_cvc')}
                                  type="text"
                                  fullWidth
                                />
                              </Grid2>
                              <Grid2 xs={6}>
                                <FormTextField
                                  id="zipCode"
                                  name="zipCode"
                                  label={t('zip_code')}
                                  type="text"
                                  fullWidth
                                />{' '}
                              </Grid2>
                            </Grid2>
                            <Typography variant="body2" component="p">
                              <Trans
                                i18nKey="subscription_and_cancellation_terms"
                                components={{
                                  linkComponent: <Link href="#" />,
                                }}
                              />
                            </Typography>
                            <Typography variant="body2" component="p">
                              {t('provider_fees')}
                            </Typography>
                            <FormSubmitLoadingButton variant="contained" size="large" type="submit">
                              {t('continue_purchase')}
                            </FormSubmitLoadingButton>
                          </AccordionDetails>
                        </Accordion>
                        <Accordion
                          square
                          className="borderVariant"
                          disableGutters
                          expanded={accordionExpanded === 'panel2'}
                          onChange={handleChangeAccordionState('panel2')}
                        >
                          <AccordionSummary
                            className="borderVariant Payment-AccordionSummary"
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls="panel2a-content"
                            id="panel2a-header"
                          >
                            <Typography className="MuiAccordionSummary-Typography">
                              {t('paypal')}
                            </Typography>
                            <PayPalLogo className="Payment-PayPalIcon" />
                          </AccordionSummary>
                          <AccordionDetails className="borderVariant Payment-AccordionDetails">
                            <Typography variant="body2" component="p">
                              <Trans
                                i18nKey="subscription_and_cancellation_terms"
                                components={{
                                  linkComponent: <Link href="#" />,
                                }}
                              />
                            </Typography>
                            <Typography variant="body2" component="p">
                              {t('provider_fees')}
                            </Typography>
                            <Typography variant="body2" component="p">
                              {t('redirect_to_paypal_hint')}
                            </Typography>
                            <Box
                              display="flex"
                              alignItems="center"
                              justifyContent="space-between"
                              flexWrap="wrap"
                            >
                              <Button variant="contained" size="large" disabled>
                                {t('continue_to_paypal')}
                              </Button>
                              <Typography variant="body2" color="error" component="span">
                                {t('currently_not_available')}
                              </Typography>
                            </Box>
                          </AccordionDetails>
                        </Accordion>
                      </CardContent>
                    </Card>
                  </Grid2>
                </Grid2>
              </div>
            </Grid2>
            <Grid2 xs={12} md={5} className="Grid-growChildren">
              <OrderSummary previewData={previewData} loading={previewLoading} />
            </Grid2>
          </Grid2>
        </Form>
      </Formik>
    </>
  );
}
