import AdminPanelSettingsTwoToneIcon from '@mui/icons-material/AdminPanelSettingsTwoTone';
import AirportShuttleTwoToneIcon from '@mui/icons-material/AirportShuttleTwoTone';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import HomeTwoToneIcon from '@mui/icons-material/HomeTwoTone';
import LogoutTwoToneIcon from '@mui/icons-material/LogoutTwoTone';
import MenuIcon from '@mui/icons-material/Menu';
import NotificationsActiveIcon from '@mui/icons-material/NotificationsActive';
import SettingsTwoToneIcon from '@mui/icons-material/SettingsTwoTone';
import AddCircleTwoToneIcon from '@mui/icons-material/AddCircleTwoTone';
import EmailTwoToneIcon from '@mui/icons-material/EmailTwoTone';
import PeopleAltTwoToneIcon from '@mui/icons-material/PeopleAltTwoTone';
import FolderSharedTwoToneIcon from '@mui/icons-material/FolderSharedTwoTone';
import LocalOfferTwoToneIcon from '@mui/icons-material/LocalOfferTwoTone';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import {
  AppBar,
  Box,
  Chip,
  Collapse,
  Divider,
  Drawer,
  IconButton,
  Link,
  List,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  ListSubheader,
  Toolbar,
  useMediaQuery,
} from '@mui/material';
import FavIcon from 'assets/favicon.svg';
import Logo from 'assets/logo.svg';
import AuthenticationApi from 'modules/authentication/api/authentication.api';
import AuthenticationContext from 'modules/authentication/state/authentication.context';
import Version from 'modules/shared/components/version/version.component';
import React, { useContext, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useLocation, useNavigate } from 'react-router';
import CustomerContext from 'modules/misc/state/customer/customer.context';
import './sidebar.component.css';
import { Link as RouterLink } from 'react-router-dom';

function ClientsList(props: { forceExtendedItems: boolean }) {
  const { t } = useTranslation();
  const { pathname } = useLocation();
  const { clientFolders, clients } = useContext(CustomerContext);
  const [extendedFolderId, setExtendedFolderId] = useState(-1);

  const toggleCollapse = (id: number) => {
    if (extendedFolderId === id) {
      setExtendedFolderId(-1);
      return;
    }
    setExtendedFolderId(id);
  };

  if (!clientFolders.length) return <></>;
  if (!clients.length) return <></>;

  return (
    <>
      {clientFolders.map((folder) => {
        const isExtended = extendedFolderId === folder.id || props.forceExtendedItems;
        return (
          <>
            <ListSubheader
              disableSticky
              className={`Sidebar-Subheader ${isExtended ? 'folderExtended' : ''}`}
              onClick={() => toggleCollapse(folder.id)}
              key={`subheader-${folder.id}`}
            >
              <FolderSharedTwoToneIcon className="PreIcon" />
              <span>{folder.name === 'Kunden' ? t('clients') : folder.name}</span>
              <ExpandMoreIcon className="ExpandIcon" />
            </ListSubheader>
            <Collapse in={isExtended} timeout="auto" unmountOnExit key={`collapse-${folder.id}`}>
              <List component="ul" disablePadding>
                <>
                  {clients
                    .filter(({ clientFolderId }) => clientFolderId === folder.id)
                    .map((client) => (
                      <Link
                        component={RouterLink}
                        to={`/${client.token}`}
                        key={`client-${client.id}`}
                      >
                        <ListItemButton selected={pathname === `/${client.token}`}>
                          <ListItemIcon>
                            <PeopleAltTwoToneIcon />
                          </ListItemIcon>
                          <ListItemText primary={client.name} />
                        </ListItemButton>
                      </Link>
                    ))}
                </>
              </List>
            </Collapse>
          </>
        );
      })}
    </>
  );
}

function SidebarContent(props: {
  isSidebarCollapsed: boolean;
  onClickLogout: () => void;
  isMobile?: boolean;
}) {
  const { t } = useTranslation();
  const { pathname } = useLocation();

  const [collapsedItemsState, setCollapsedItemsState] = useState({
    settings: false,
    clients: false,
  });
  const toggleCollapsedItemsState = (key) => {
    if (!props.isMobile) {
      setCollapsedItemsState({ ...collapsedItemsState, [key]: !collapsedItemsState[key] });
    }
  };
  const forceExtendedItems = props.isSidebarCollapsed || props.isMobile || false;

  return (
    <div className="Sidebar-ScrollContainer">
      <div className="Sidebar-Content">
        <Link component={RouterLink} to="/" className="Sidebar-LogoWrapper">
          {props.isSidebarCollapsed ? <FavIcon /> : <Logo />}
        </Link>
        <List>
          <Link component={RouterLink} to="/">
            <ListItemButton selected={pathname === '/'}>
              <ListItemIcon>
                <HomeTwoToneIcon />
              </ListItemIcon>
              <ListItemText primary={t('home')} />
            </ListItemButton>
          </Link>

          <ClientsList forceExtendedItems={forceExtendedItems} />
          <Link component={RouterLink} to="clients-add">
            <ListItemButton selected={pathname === '/clients-add'}>
              <ListItemIcon>
                <AddCircleTwoToneIcon />
              </ListItemIcon>
              <ListItemText primary={t('add_client')} />
            </ListItemButton>
          </Link>

          <ListSubheader
            disableSticky
            className="Sidebar-Subheader"
            onClick={() => toggleCollapsedItemsState('settings')}
          >
            <SettingsTwoToneIcon className="PreIcon" />
            {t('settings')}
          </ListSubheader>
          <Collapse
            in={!collapsedItemsState.settings || forceExtendedItems}
            timeout="auto"
            unmountOnExit
          >
            <List component="ul" disablePadding>
              <Link component={RouterLink} to="/email">
                <ListItemButton selected={pathname === '/email'}>
                  <ListItemIcon>
                    <EmailTwoToneIcon />
                  </ListItemIcon>
                  <ListItemText primary="E-Mail" />
                </ListItemButton>
              </Link>
              <Link component={RouterLink} to="/status">
                <ListItemButton selected={pathname === '/status'}>
                  <ListItemIcon>
                    <LocalOfferTwoToneIcon />
                  </ListItemIcon>
                  <ListItemText primary="Status" />
                </ListItemButton>
              </Link>
              <Link component={RouterLink} to="/">
                <ListItemButton>
                  <ListItemIcon>
                    <SettingsTwoToneIcon />
                  </ListItemIcon>
                  <ListItemText primary="example" />
                </ListItemButton>
              </Link>
              <ListItemButton>
                <ListItemIcon>
                  <AdminPanelSettingsTwoToneIcon />
                </ListItemIcon>
                <ListItemText primary="Admin panel" secondary="Only Admin can see this" />
              </ListItemButton>
              <ListItemButton>
                <ListItemIcon>
                  <AirportShuttleTwoToneIcon />
                </ListItemIcon>
                <ListItemText primary="Camping" />
                <Chip icon={<NotificationsActiveIcon />} label={t('new')} color="secondary" />
              </ListItemButton>
            </List>
          </Collapse>
        </List>

        <Box marginTop="auto" marginBottom="4px">
          <Version />
        </Box>
        <Divider sx={{ marginBottom: '4px' }} />
        <List component="ul" disablePadding sx={{ marginBottom: '12px' }}>
          <ListItemButton onClick={props.onClickLogout}>
            <ListItemIcon>
              <LogoutTwoToneIcon />
            </ListItemIcon>
            <ListItemText primary={t('logout')} />
          </ListItemButton>
        </List>
      </div>
    </div>
  );
}

export default function Sidebar(props: { isCollapsed: boolean; toggleCollapse: () => void }) {
  const { onLogout } = useContext(AuthenticationContext);
  const { isCollapsed, toggleCollapse } = props;
  const [isMobileSidebarOpen, setIsMobileSidebarOpen] = useState(false);
  const minWidth600 = useMediaQuery('(min-width:600px)');

  const navigate = useNavigate();
  const onClickLogout = async () => {
    await AuthenticationApi.Logout();
    navigate('/');
    onLogout();
  };

  if (!minWidth600) {
    return (
      <AppBar component="header" className="MobileHeader">
        <Toolbar>
          <IconButton onClick={() => setIsMobileSidebarOpen(!isMobileSidebarOpen)}>
            <MenuIcon />
          </IconButton>
          <Link component={RouterLink} to="/" className="MobileHeader-logo">
            <FavIcon />
          </Link>
        </Toolbar>
        <Drawer
          anchor="left"
          open={isMobileSidebarOpen}
          onClose={() => setIsMobileSidebarOpen(false)}
          className="Sidebar"
        >
          <SidebarContent isSidebarCollapsed={isCollapsed} onClickLogout={onClickLogout} isMobile />
        </Drawer>
      </AppBar>
    );
  }

  return (
    <nav className={`Sidebar ${isCollapsed ? 'collapsed' : ''}`}>
      <IconButton
        aria-label="delete"
        size="small"
        className="Sidebar-collapseButton"
        onClick={toggleCollapse}
      >
        {isCollapsed ? <ChevronRightIcon /> : <ChevronLeftIcon />}
      </IconButton>
      <div className="Sidebar-Wrapper">
        <SidebarContent isSidebarCollapsed={isCollapsed} onClickLogout={onClickLogout} />
      </div>
    </nav>
  );
}
