import DeleteOutlineOutlinedIcon from '@mui/icons-material/DeleteOutlineOutlined';
import EditIcon from '@mui/icons-material/Edit';
import {
  Box,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Tooltip,
  useMediaQuery,
} from '@mui/material';
import Color from 'color';
import { CaseStatus } from 'modules/shared/api/case.api';
import ButtonMenu from 'modules/shared/components/button-menu/button-menu.component';
import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';

function ColorPill(props: {
  value: string | number;
  statusTitle?: string;
  sx?: Record<string, string>;
}) {
  const { t } = useTranslation();
  const backgroundColor = useMemo(() => {
    const color = new Color(props.value);
    return color.rgb().fade(0.8).string();
  }, [props.value]);
  return (
    <Box
      sx={{
        backgroundColor,
        color: props.value,
        borderRadius: '16px',
        padding: '0 8px',
        textOverflow: 'ellipsis',
        overflow: 'hidden',
        maxWidth: '200px',
        ...props.sx,
      }}
    >
      {props.statusTitle || t('status_name')}
    </Box>
  );
}

function EnhancedTableHead() {
  const minWidth600 = useMediaQuery('(min-width:600px)');
  const { t } = useTranslation();
  const headCells = useMemo(() => {
    if (minWidth600) {
      return [
        {
          id: 'label',
          label: t('status_name'),
        },
        {
          id: 'color',
          label: t('preview'),
        },
        {
          id: 'action',
          label: 'Aktion',
        },
      ];
    }
    return [
      {
        id: 'label',
        label: t('status'),
      },
      {
        id: 'action',
        label: '',
      },
    ];
  }, [minWidth600]);

  return (
    <TableHead sx={{ backgroundColor: 'var(--background-color--white)' }}>
      <TableRow>
        {headCells.map((headCell) => (
          <TableCell key={headCell.id} align="left">
            {headCell.label}
          </TableCell>
        ))}
      </TableRow>
    </TableHead>
  );
}

type CaseStatusTableProps = {
  statuses: CaseStatus[];
  onDelete: (id: number) => void;
  onEdit: (id: number) => void;
};

export default function CaseStatusTable(props: CaseStatusTableProps) {
  const minWidth600 = useMediaQuery('(min-width:600px)');
  const { t } = useTranslation();

  const onSelectButtonMenu = (key: string, statusId: number) => {
    switch (key) {
      case 'edit': {
        props.onEdit(statusId);
        break;
      }
      case 'delete': {
        props.onDelete(statusId);
        break;
      }
      default:
        break;
    }
  };

  return (
    <TableContainer>
      <Table sx={{ minWidth: minWidth600 ? 750 : '100%' }} aria-labelledby="tableTitle">
        <EnhancedTableHead />
        <TableBody>
          {props.statuses.map((row) => (
            <TableRow hover key={row.id}>
              {minWidth600 && (
                <>
                  <TableCell>{row.label}</TableCell>
                  <TableCell>
                    <Box
                      sx={{ display: 'flex', flexDirection: 'row', gap: '.5rem', height: '100%' }}
                    >
                      <ColorPill
                        value={row.color}
                        statusTitle={`${(Math.random() * 100).toFixed(0)}`}
                        sx={{ minWidth: '18px', textAlign: 'center' }}
                      />
                      <ColorPill value={row.color} statusTitle={row.label} />
                    </Box>
                  </TableCell>

                  <TableCell>
                    <Tooltip title={t('edit')}>
                      <IconButton
                        aria-label="Edit Status"
                        color="primary"
                        onClick={() => props.onEdit(row.id)}
                      >
                        <EditIcon />
                      </IconButton>
                    </Tooltip>
                    <Tooltip title={t('delete')}>
                      <IconButton
                        aria-label="Delete Status"
                        color="error"
                        onClick={() => props.onDelete(row.id)}
                      >
                        <DeleteOutlineOutlinedIcon />
                      </IconButton>
                    </Tooltip>
                  </TableCell>
                </>
              )}

              {!minWidth600 && (
                <>
                  <TableCell>
                    <Box
                      sx={{ display: 'flex', flexDirection: 'row', gap: '.5rem', height: '100%' }}
                    >
                      <ColorPill value={row.color} statusTitle={row.label} />
                    </Box>
                  </TableCell>
                  <TableCell sx={{ width: '0' }}>
                    <ButtonMenu
                      onSelect={(key: string) => onSelectButtonMenu(key, row.id)}
                      options={[
                        {
                          label: t('edit'),
                          key: 'edit',
                          icon: EditIcon,
                          iconSx: { color: 'var(--primary-color)' },
                        },
                        {
                          label: t('delete'),
                          key: 'delete',
                          icon: DeleteOutlineOutlinedIcon,
                          iconSx: { color: 'var(--error-color)' },
                        },
                      ]}
                    />
                  </TableCell>
                </>
              )}
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
