import { Box, InputLabel } from '@mui/material';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import { red } from '@mui/material/colors';
import Color from 'color';
import { Field, Form, Formik } from 'formik';
import { CaseStatus } from 'modules/shared/api/case.api';
import FormTextField from 'modules/shared/components/form-text-field/form-text-field.component';
import * as React from 'react';
import { useTranslation } from 'react-i18next';
import './case-status-edit-modal.component.css';

type CaseStatusEditModalProps = {
  status?: CaseStatus;
  handleClose: () => void;
  handleSave: (status: CaseStatus) => void;
};

const ColorOptions = [
  { label: red[500], value: red[500] },
  { label: '#8B8982', value: '#8B8982' },
  { label: '#EF233C', value: '#EF233C' },
  { label: '#39A0ED', value: '#39A0ED' },
  { label: '#FC814A', value: '#FC814A' },
  { label: '#ECA400', value: '#ECA400' },
  { label: '#35CE8D', value: '#35CE8D' },
];

export default function CaseStatusEditModal(props: CaseStatusEditModalProps) {
  const { t } = useTranslation();
  const isOpen = !!props.status;

  const initialValues: CaseStatus | undefined = {
    ...props.status,
  };

  return (
    <Dialog open={isOpen} onClose={props.handleClose}>
      <DialogTitle>{t('edit_status')}</DialogTitle>
      <Formik
        initialValues={initialValues || {}}
        enableReinitialize
        onSubmit={async (values: CaseStatus) => {
          props.handleSave({
            color: values.color,
            label: values.label,
            id: values.id,
          });
          props.handleClose();
        }}
      >
        {({ values }) => (
          <Form>
            <DialogContent>
              <DialogContentText>{t('edit_status_hint')}</DialogContentText>
              <Box sx={{ marginTop: '1rem' }}>
                <FormTextField
                  name="label"
                  id="label"
                  placeholder={`${t('name')}`}
                  variant="standard"
                  label={t('status_name')}
                  required
                />
                <InputLabel focused shrink required>
                  {t('status_color')}
                </InputLabel>
                <Field name="color">
                  {({ field }) => (
                    <Box
                      sx={{
                        display: 'flex',
                        flexDirection: 'row',
                        flexWrap: 'wrap',
                        gap: '.5rem',
                        alignItems: 'center',
                      }}
                    >
                      {ColorOptions.map((option) => {
                        const backgroundColor = new Color(option.value).rgb().string();
                        const isSelected = option.value === values.color;
                        return (
                          <Box
                            key={option.value}
                            sx={{
                              borderRadius: '16px',
                              padding: '2px 2px',
                              border: isSelected
                                ? `2px solid rgba(0, 0, 0, 0.5)`
                                : `1px solid rgba(0, 0, 0, 0.12)`,
                              flexGrow: '0',
                              cursor: 'pointer',
                            }}
                            className="CaseStatusColorHoverClass"
                            onClick={() =>
                              field.onChange({ target: { value: option.value, name: 'color' } })
                            }
                          >
                            <Box
                              sx={{
                                backgroundColor,
                                borderRadius: '16px',
                                height: '22px',
                                width: '40px',
                              }}
                            />
                          </Box>
                        );
                      })}
                    </Box>
                  )}
                </Field>
              </Box>
            </DialogContent>
            <DialogActions>
              <Button onClick={props.handleClose}>{t('cancel')}</Button>
              <Button variant="contained" type="submit">
                {t('save')}
              </Button>
            </DialogActions>
          </Form>
        )}
      </Formik>
    </Dialog>
  );
}
