import { LoadingButton, LoadingButtonProps } from '@mui/lab';
import { FormikContext } from 'formik';
import React, { useContext } from 'react';

/**
 * Submit loading button within formik context.
 * @param props
 * @returns
 */
export default function FormSubmitLoadingButton(props: LoadingButtonProps) {
  const { isSubmitting, isValidating } = useContext(FormikContext);
  const disabled = [props.disabled, isSubmitting, isValidating].some(Boolean);

  return (
    <LoadingButton
      loading={false}
      type="submit"
      loadingPosition="center"
      variant={props.variant || 'contained'}
      disabled={disabled}
      color={props.color || 'primary'}
      size={props.size || 'large'}
    >
      <span>{props.children}</span>
    </LoadingButton>
  );
}
