import { Box, Button, Card, CardHeader, Container, Select, Typography } from '@mui/material';
import { Form, Formik } from 'formik';
import CustomerApi from 'modules/shared/api/customer.api';
import CustomerContext from 'modules/misc/state/customer/customer.context';
import FormSelect from 'modules/shared/components/form-select/form-select.component';
import FormSubmitButton from 'modules/shared/components/form-submit-button/form-submit-button.component';
import FormTextField from 'modules/shared/components/form-text-field/form-text-field.component';
import { AlertFeedback } from 'modules/shared/feedback/alert-feedback';
import React, { useContext, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router';

type FormValues = {
  companyName: string;
  folderId?: number;
};

export default function ClientsAddPage() {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const customerContext = useContext(CustomerContext);
  const { clientFolders } = customerContext;

  const selectOptions = useMemo(
    () =>
      clientFolders.map((folder) => ({
        value: folder.id,
        label: folder.name === 'Kunden' ? t('clients') : folder.name,
      })),
    [clientFolders]
  );
  const onCancel = () => navigate(-1);
  return (
    <Container>
      <Typography variant="h4" component="h1" sx={{ marginBottom: '2rem' }}>
        {t('add_client')}
      </Typography>

      <Card className="FormCard" elevation={0}>
        <CardHeader title={t('please_fill_the_fields')} />
        <Formik<FormValues>
          initialValues={{
            companyName: '',
            folderId: clientFolders[0]?.id,
          }}
          onSubmit={async (values) => {
            try {
              const result = await CustomerApi.AddClientToCustomer(
                values.companyName,
                values.folderId
              );
              AlertFeedback.success.added();
              customerContext.addClient(result.data.response);
            } catch (error) {
              AlertFeedback.error.defaultError();
            }
          }}
          validate={() => {}}
          validateOnBlur={true}
          validateOnChange={false}
          enableReinitialize
        >
          <Form>
            <FormTextField
              id="companyName"
              name="companyName"
              label={t('name')}
              type="text"
              fullWidth
              required
              focus={true}
              autoComplete="false"
            />
            <FormSelect
              options={selectOptions}
              name="folderId"
              id="folderId"
              label={t('folder')}
              helperText={`${t('folder_hint')}`}
            />

            <Box display="flex" justifyContent="end">
              <Button variant="text" sx={{ marginRight: '1rem' }} size="large" onClick={onCancel}>
                {t('cancel')}
              </Button>
              <FormSubmitButton label={t('add')} />
            </Box>
          </Form>
        </Formik>
      </Card>
    </Container>
  );
}
