import { Container } from '@mui/material';
import Sidebar from 'modules/app/components/sidebar/sidebar.component';
import React, { useState } from 'react';
import { Outlet } from 'react-router';
import './root.page.css';
import CustomerContext, { useCustomerContext } from 'modules/misc/state/customer/customer.context';

export default function RootPage() {
  const [isSidebarCollapsed, setIsSidebarCollapsed] = useState(false);
  const toggleSidebarCollapse = () => setIsSidebarCollapsed(!isSidebarCollapsed);

  const customerContextValue = useCustomerContext();

  return (
    <CustomerContext.Provider value={customerContextValue}>
      <div className="PageLayout">
        <Sidebar isCollapsed={isSidebarCollapsed} toggleCollapse={toggleSidebarCollapse} />
        <main className={`AppPage-Content ${isSidebarCollapsed ? '--sidebarCollapsed' : ''}`}>
          <Container>
            <Outlet />
          </Container>
        </main>
      </div>
    </CustomerContext.Provider>
  );
}
