import { Box, Card, CardHeader, Typography } from '@mui/material';
import { Form, Formik } from 'formik';
import AuthenticationApi from 'modules/authentication/api/authentication.api';
import FormPasswordInput from 'modules/shared/components/form-password-input/form-password-input.component';
import FormSubmitButton from 'modules/shared/components/form-submit-button/form-submit-button.component';
import FormTextField from 'modules/shared/components/form-text-field/form-text-field.component';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useNavigate, useSearchParams } from 'react-router-dom';
import isEmail from 'validator/lib/isEmail';
import './register.page.css';

export type FormValues = {
  email: string;
  password: string;
  passwordRepeat: string;
  name: string;
  firstname: string;
  companyName: string;
};

export default function RegisterPage() {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const paramsEmail = searchParams.get('email');
  const navigate = useNavigate();

  const onValidate = (values: FormValues) => {
    const errors: Record<string, string> = {};
    if (values.password !== values.passwordRepeat) {
      errors.passwordRepeat = t('passwords_not_match');
    }
    if (values.email && !isEmail(values.email)) {
      errors.email = t('email_invalid');
    }
    return errors;
  };

  return (
    <>
      <Box my="1em">
        <Typography variant="h3" component="h1" textAlign="center">
          {t('sign_up_title')}
        </Typography>
      </Box>
      <Box my="1em">
        <Typography variant="body1" component="h4" textAlign="center" mb={3}>
          {t('please_fill_out_data')}
        </Typography>
      </Box>

      <Box display="flex" justifyContent="center">
        <Formik<FormValues>
          initialValues={{
            email: '',
            password: '',
            passwordRepeat: '',
            name: '',
            firstname: '',
            companyName: '',
          }}
          onSubmit={async (values) => {
            const email = await AuthenticationApi.Register(
              values.email,
              values.password,
              values.name,
              values.firstname,
              values.companyName
            );
            if (email) {
              navigate(`/?email=${email}`);
            }
          }}
          validate={onValidate}
          validateOnBlur={true}
          validateOnChange={false}
        >
          <Form>
            <Card className="FormCard">
              <CardHeader title={t('account_data')} />

              <FormTextField
                id="email"
                name="email"
                label={t('email')}
                type="email"
                fullWidth
                required
                focus={!paramsEmail}
              />
              <FormPasswordInput
                name="password"
                label={t('password')}
                id="password"
                autoComplete
                required
              />
              <FormPasswordInput
                name="passwordRepeat"
                label={t('password_repeat')}
                id="passwordRepeat"
                autoComplete
                required
              />

              <Box marginTop="1rem">
                <CardHeader title={t('business_data')} />
              </Box>

              <FormTextField
                id="name"
                name="name"
                label={t('lastname')}
                type="text"
                fullWidth
                required
              />
              <FormTextField
                id="firstname"
                name="firstname"
                label={t('firstname')}
                type="text"
                fullWidth
                required
              />
              <FormTextField
                id="companyName"
                name="companyName"
                label={t('companyName')}
                type="text"
                fullWidth
                required
              />

              <div className="Login-ActionContainer">
                <FormSubmitButton label={t('login')} />
              </div>
            </Card>
          </Form>
        </Formik>
      </Box>
    </>
  );
}
