import axios from 'axios';
import { ClientFolderType } from '../../misc/state/customer/customer.context';

export type ClientData = {
  id: number;
  name: string;
  token: string;
  clientFolderId: number;
};

export type Customer = {
  id: number;
  token: string;
  name: string;
};

/**
 * Load the customer of the user
 * @param values
 */
async function LoadCustomerOfUser() {
  return axios.get<{ response: Customer; reference: string }>('/customer');
}

/**
 * Load a list of all clients the customer has
 * @param values
 */
async function LoadListOfClients() {
  return axios.get<{ response: ClientData[]; reference: string }>('/customer/client/list');
}

/**
 * Load a list of all clients the customer has
 * @param values
 */
async function AddClientToCustomer(name: string, folderId: number) {
  return axios.post<{ response: ClientData; reference: string }>('/customer/client', {
    name,
    folderId,
  });
}

/**
 * Load a list of all client folders
 * @param values
 */
async function LoadListOfClientFolders() {
  return axios.get<{ response: ClientFolderType[]; reference: string }>('/customer/client/folder');
}

type CustomerApiType = {
  LoadListOfClients: typeof LoadListOfClients;
  LoadCustomerOfUser: typeof LoadCustomerOfUser;
  AddClientToCustomer: typeof AddClientToCustomer;
  LoadListOfClientFolders: typeof LoadListOfClientFolders;
};

const CustomerApi: CustomerApiType = {
  LoadListOfClients,
  LoadCustomerOfUser,
  AddClientToCustomer,
  LoadListOfClientFolders,
};

export default CustomerApi;
