import axios from 'axios';
import { AlertFeedback } from 'modules/shared/feedback/alert-feedback';
import User from 'modules/shared/user/types/user.type';

/**
 * Login user
 * @param email
 * @param password
 */
async function Login(email: string, password: string) {
  try {
    const user = await axios.post<User & { apikey: string }>('auth/login', { email, password });
    AlertFeedback.success.welcome();
    return user;
  } catch (error) {
    AlertFeedback.error.defaultError();
  }
}

/**
 * Validate user using id and apikey from cookies
 */
async function Validate() {
  const user = await axios.post<{ response: User & { apikey: string }; reference: string }>(
    'auth/validate'
  );
  return user;
}

/**
 * Logout user
 */
async function Logout() {
  try {
    await axios.post('auth/logout');
    AlertFeedback.success.logout();
  } catch (error) {
    AlertFeedback.error.defaultError();
  }
}

/**
 * Register user. Returns email.
 * @param email
 * @param password
 * @param name
 */
async function Register(
  email: string,
  password: string,
  name: string,
  firstname: string,
  companyName: string
): Promise<string | undefined> {
  try {
    const result = await axios.post<{ response: { email: string }; reference: string }>(
      'auth/register',
      {
        email,
        password,
        name,
        firstname,
        companyName,
      }
    );
    AlertFeedback.success.register();
    return result.data.response.email;
  } catch (error) {
    AlertFeedback.error.defaultError();
  }
}

/**
 * Reset password
 * @param email
 */
async function ResetPassword(email: string) {
  try {
    await axios.post('/reset-password', { email });
  } catch (error) {
    AlertFeedback.error.defaultError();
  }
}

/**
 * Confirm Email
 * @param email
 * @param token
 */
async function ConfirmEmail(email: string, token: string) {
  try {
    await axios.post('/confirm-email', { email, token });
  } catch (error) {
    AlertFeedback.error.defaultError();
  }
}

type AuthenticationApiType = {
  Login: typeof Login;
  Register: typeof Register;
  ResetPassword: typeof ResetPassword;
  ConfirmEmail: typeof ConfirmEmail;
  Logout: typeof Logout;
  Validate: typeof Validate;
};

const AuthenticationApi: AuthenticationApiType = {
  Login,
  Register,
  ResetPassword,
  ConfirmEmail,
  Logout,
  Validate,
};

export default AuthenticationApi;
