import AccountBoxIcon from '@mui/icons-material/AccountBox';
import CreditCardIcon from '@mui/icons-material/CreditCard';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import VpnKeyIcon from '@mui/icons-material/VpnKey';
import { Box, Breadcrumbs, Container, Link, Tab, Tabs, Typography } from '@mui/material';
import React from 'react';
import { Outlet, useLocation, useNavigate } from 'react-router';

export default function SettingsPage() {
  const location = useLocation();
  const [activeTab, setActiveTab] = React.useState(() => {
    const pathnames = location.pathname.split('/').filter((x) => x);
    if (pathnames.includes('billing')) return 1;
    if (pathnames.includes('password')) return 2;
    return 0;
  });
  const navigate = useNavigate();

  const handleChange = (event: React.SyntheticEvent, newActiveTab: number) => {
    setActiveTab(newActiveTab);
    switch (newActiveTab) {
      case 1:
        navigate('billing');
        return;
      case 2:
        navigate('password');
        return;
      default:
        navigate('');
    }
  };

  return (
    <React.Fragment>
      <Container>
        <Box>
          <Typography variant="h4" component="h1">
            Settings
          </Typography>
          <Breadcrumbs separator={<NavigateNextIcon fontSize="small" />} aria-label="breadcrumb">
            <Link underline="hover" color="inherit" href="/">
              Dashboard
            </Link>
            <Link underline="hover" color="inherit" href="/settings">
              Settings
            </Link>
          </Breadcrumbs>
        </Box>
        <Box sx={{ mt: '1.5rem', mb: '1.5rem' }}>
          <Tabs
            value={activeTab}
            onChange={handleChange}
            aria-label="settings-tabs"
            variant="scrollable"
            scrollButtons="auto"
            allowScrollButtonsMobile
          >
            <Tab icon={<AccountBoxIcon />} iconPosition="start" label="General" />
            <Tab icon={<CreditCardIcon />} iconPosition="start" label="Billing" />
            <Tab icon={<VpnKeyIcon />} iconPosition="start" label="Change Password" />
          </Tabs>
        </Box>
        <Outlet />
      </Container>
    </React.Fragment>
  );
}
