import axios from 'axios';
import { Cadence, Package } from 'modules/authentication/pages/packages/packages.page';

export type CostPreviewData = {
  package: Package;
  cadence: Cadence;
  taxes: number;
  country: string;
  bruttoYearly: number;
  bruttoMonthly: number;
  nettoYearly: number;
  nettoMonthly: number;
  taxesYearly: number;
  taxesMonthly: number;
};

/**
 * Load a preview of the cost to expect
 * @param values
 */
async function LoadCostPreview(selectedPackage: Package, cadence: Cadence, country: string) {
  return axios.get<{ response: CostPreviewData; reference: string }>('/payment/preview', {
    params: {
      package: selectedPackage,
      cadence,
      country,
    },
  });
}

type PaymentApiType = {
  LoadCostPreview: typeof LoadCostPreview;
};

const PaymentApi: PaymentApiType = {
  LoadCostPreview,
};

export default PaymentApi;
