import {
  Box,
  Button,
  Card,
  CardHeader,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@mui/material';
import MailApi, { MailConfigType, MailConfiguration } from 'modules/shared/api/mail.api';
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';

export default function ActiveConnectionCard({
  configuration,
  syncFunction,
}: {
  configuration: MailConfiguration;
  syncFunction: () => Promise<void>;
}) {
  const { t } = useTranslation();
  const [loading, setLoading] = useState(false);

  const dateToDDMMYYYY = (dateString: string) => {
    const date = new Date(dateString);
    return `${date.getDate()}.${date.getMonth() + 1}.${date.getFullYear()}`;
  };

  const onSync = async () => {
    setLoading(true);
    await syncFunction();
    setLoading(false);
  };

  return (
    <Card className="FormCard EmailCard" elevation={0}>
      <CardHeader className="ActiveCardHeader" title={t('active_connection')} />
      <TableContainer>
        <Table sx={{}} aria-label="connection_props">
          <TableBody>
            <TableRow sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
              <TableCell component="td" scope="row">
                {t('connection_type')}
              </TableCell>
              <TableCell align="right" sx={{ textTransform: 'capitalize' }}>
                {configuration.type.toLowerCase()}
              </TableCell>
            </TableRow>
            <TableRow sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
              <TableCell component="td" scope="row">
                {t('created_at')}
              </TableCell>
              <TableCell align="right">{dateToDDMMYYYY(configuration.createdAt)}</TableCell>
            </TableRow>
            <TableRow sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
              <TableCell component="td" scope="row">
                {t('last_sync')}
              </TableCell>
              <TableCell align="right">{dateToDDMMYYYY(configuration.lastSync)}</TableCell>
            </TableRow>
            <TableRow sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
              <TableCell component="td" scope="row">
                {t('amount_of_mails')}
              </TableCell>
              <TableCell align="right">{configuration.mailsCount}</TableCell>
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>
      <Box display="flex" justifyContent="end" marginTop="1rem">
        <Button variant="contained" onClick={onSync} disabled={loading}>
          {t('sync_emails')}
        </Button>
      </Box>
    </Card>
  );
}
