import { Box, Card, CardContent, CardHeader, Divider, Skeleton, Typography } from '@mui/material';
import Logo from 'assets/logo.svg';
import { CostPreviewData } from 'modules/misc/api/payment.api';
import centToEuro from 'modules/shared/helpers/validator/cent_to_euro.helper';
import React from 'react';
import { useTranslation } from 'react-i18next';
import EasySkeleton from 'modules/shared/components/easy-skeleton/easy-skeleton.component';
import Loading from 'modules/shared/components/loading/loading-spinner.component';
import { Cadence } from '../../packages/packages.page';

export default function OrderSummary(props: { previewData?: CostPreviewData; loading: boolean }) {
  const { previewData, loading } = props;
  const { t } = useTranslation();

  const isMonthlyCadence = previewData?.cadence === Cadence.monthly;
  const isYearlyCadence = previewData?.cadence === Cadence.yearly;

  return (
    <Card>
      <CardHeader title={t('order_summary')} />
      <CardContent className="OrderSummary">
        {loading ? (
          <Box display="flex" justifyContent="center">
            <Loading loading={loading} />
          </Box>
        ) : (
          <>
            <Box display="flex" alignItems="center" gap="1rem" mb="1rem">
              <Logo className="OrderSummary-Logo" />
              <Typography variant="h6">-</Typography>

              <Typography variant="h6">
                {t(`package_${previewData?.package || 0}`)} {t('package')}
              </Typography>
            </Box>
            <Box display="flex" alignItems="center" justifyContent="space-between" mb="1rem">
              <Typography variant="body2">
                {isMonthlyCadence &&
                  t('order_monthly_cost', { price: centToEuro(previewData?.bruttoMonthly || 0) })}
                {isYearlyCadence &&
                  t('order_yearly_cost', { price: centToEuro(previewData?.bruttoMonthly || 0) })}
              </Typography>
              <Typography variant="h6">
                {isMonthlyCadence && `${centToEuro(previewData?.bruttoMonthly)} `}
                {isYearlyCadence && `${centToEuro(previewData?.bruttoYearly)} `}
              </Typography>
            </Box>
            <Divider />
            <Box display="flex" alignItems="end" justifyContent="space-between" mb="1rem" mt="1rem">
              <Typography variant="body2">{t('country_for_taxes')}</Typography>
              <Typography variant="body2">{t(previewData?.country || 'Germany')}</Typography>
            </Box>
            <Box display="flex" alignItems="center" justifyContent="space-between" mb="1rem">
              <Typography variant="body2">
                {t('vat_with_amount', { amount: (previewData?.taxes || 0) * 100 })}
              </Typography>
              <Typography variant="body2" sx={{ fontWeight: 700 }}>
                {isMonthlyCadence && `${centToEuro(previewData?.taxesMonthly)} `}
                {isYearlyCadence && `${centToEuro(previewData?.taxesYearly)} `}
              </Typography>
            </Box>
            <Box display="flex" alignItems="center" justifyContent="space-between" mb="1rem">
              <Typography variant="h6">{t('total_cost')}</Typography>
              <Typography variant="h4">
                {isMonthlyCadence && `${centToEuro(previewData?.nettoMonthly)} *`}
                {isYearlyCadence && `${centToEuro(previewData?.nettoYearly)} *`}
              </Typography>
            </Box>
            <Box display="flex" alignItems="center" justifyContent="space-between">
              <Typography variant="body2" color="GrayText">
                *{isMonthlyCadence && t('billed_monthly_hint')}
                {isYearlyCadence && t('billed_yearly_hint')}
              </Typography>
            </Box>
          </>
        )}
      </CardContent>
    </Card>
  );
}
