import { Checkbox, CheckboxProps, FormControlLabel, TextField } from '@mui/material';
import { Field } from 'formik';
import React from 'react';

type FormCheckboxProps = CheckboxProps & {
  label: string;
  name: string;
  className?: string;
};

/**
 * Checkbox to use in formik context
 * @param props
 * @returns
 */
export default function FormCheckbox(props: FormCheckboxProps) {
  return (
    <Field name={props.name}>
      {({ field }) => (
        <FormControlLabel
          className={props.className}
          control={
            <Checkbox
              id={props.id || props.name}
              defaultValue={props.defaultValue}
              color={props.color}
              required={props.required}
              onChange={(event) => field.onChange(event)}
              checked={field.value}
              inputProps={props.inputProps}
              size={props.size}
              defaultChecked={props.defaultChecked}
            />
          }
          label={props.label}
        />
      )}
    </Field>
  );
}
