import React, { useEffect, useState } from 'react';

/**
 * Component to delay the appeareance of a component
 * @param props
 */
export default function Delay(props: { delay?: number; children: React.ReactNode }) {
  const [visible, setVisible] = useState(false);

  useEffect(() => {
    const timeout = window.setTimeout(() => setVisible(true), props.delay || 1000);
    return () => {
      window.clearTimeout(timeout);
    };
  }, []);

  if (visible) return <>{props.children}</>;
  return <></>;
}
