import axios from 'axios';
import { AlertFeedback } from '../feedback/alert-feedback';

export type CaseStatus = {
  id: number;
  label: string;
  color: string;
  protected?: boolean;
};

async function GetCaseStatuses() {
  try {
    const result = await axios.get<{ response: CaseStatus[]; reference: string }>('/case/status');
    return result.data.response;
  } catch (error) {
    AlertFeedback.error.defaultError();
    return [];
  }
}

async function EditCaseStatus(status: CaseStatus) {
  const result = await axios.put<{ response: CaseStatus; reference: string }>('/case/status', {
    ...status,
  });
  return result.data.response;
}

async function AddCaseStatus(status: Omit<CaseStatus, 'id'>) {
  const result = await axios.post<{ response: CaseStatus; reference: string }>('/case/status', {
    ...status,
  });
  return result.data.response;
}

async function DeleteCaseStatus(id: number) {
  await axios.delete<{ reference: string }>('/case/status', { data: { id } });
}

type CaseApiType = {
  GetCaseStatuses: typeof GetCaseStatuses;
  DeleteCaseStatus: typeof DeleteCaseStatus;
  EditCaseStatus: typeof EditCaseStatus;
  AddCaseStatus: typeof AddCaseStatus;
};

const CaseApi: CaseApiType = {
  GetCaseStatuses,
  DeleteCaseStatus,
  EditCaseStatus,
  AddCaseStatus,
};

export default CaseApi;
