import MoreVertIcon from '@mui/icons-material/MoreVert';
import { ListItemIcon, ListItemText, SvgIconTypeMap, Typography } from '@mui/material';
import IconButton from '@mui/material/IconButton';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import { OverridableComponent } from '@mui/material/OverridableComponent';
import React from 'react';

type ButtonMenuProps = {
  options: {
    label: string;
    icon?: OverridableComponent<SvgIconTypeMap> & {
      muiName: string;
    };
    key: string;
    subLabel?: string;
    sx?: Record<string, string>;
    labelSx?: Record<string, string>;
    iconSx?: Record<string, string>;
  }[];
  onSelect: (key: string) => void;
};

export default function ButtonMenu(props: ButtonMenuProps) {
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = (key: string) => {
    props.onSelect(key);
    setAnchorEl(null);
  };

  return (
    <div>
      <IconButton
        aria-label="more"
        id="long-button"
        aria-controls={open ? 'long-menu' : undefined}
        aria-expanded={open ? 'true' : undefined}
        aria-haspopup="true"
        onClick={handleClick}
      >
        <MoreVertIcon />
      </IconButton>
      <Menu
        id="long-menu"
        MenuListProps={{
          'aria-labelledby': 'long-button',
        }}
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
      >
        {props.options.map((option) => (
          <MenuItem
            key={option.key}
            id={option.key}
            onClick={() => handleClose(option.key)}
            sx={{ ...(option.sx || {}) }}
          >
            {option.icon && (
              <ListItemIcon sx={{ ...(option.iconSx || {}) }}>
                <option.icon fontSize="small" />
              </ListItemIcon>
            )}
            <ListItemText sx={{ ...(option.labelSx || {}) }}>{option.label}</ListItemText>
            {!!option.subLabel && <Typography variant="body2">{option.subLabel}</Typography>}
          </MenuItem>
        ))}
      </Menu>
    </div>
  );
}
