import { AppBar, Box, Divider, Link, Toolbar, Typography, useMediaQuery } from '@mui/material';
import Logo from 'assets/logo.svg';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { Link as RouterLink, useLocation } from 'react-router-dom';
import './light-header.component.css';
import LanguageSwitcher from 'modules/shared/components/language-switcher/language-switcher.component';

export default function LightHeader() {
  const minWidth600 = useMediaQuery('(min-width:600px)');
  const { t } = useTranslation();

  const location = useLocation();
  const isRegister = location.pathname.includes('register');

  return (
    <AppBar component="header" className="LightHeader" color="transparent" position="relative">
      <Toolbar>
        <div className="LightHeader-LogoWrapper">
          <RouterLink to="/">
            <Logo />
          </RouterLink>
        </div>
        {/* <Box marginRight="1rem" display="flex">
          <LanguageSwitcher />
        </Box> */}
        <Box className="LightHeader-LoginBox">
          <Typography variant={minWidth600 ? 'subtitle1' : 'subtitle2'}>
            {isRegister ? t('have_an_account') : t('dont_have_account')}
          </Typography>
          <Typography variant={minWidth600 ? 'subtitle1' : 'subtitle2'}>
            {isRegister ? (
              <Link href="/">{t('login')}</Link>
            ) : (
              <Link href="/register">{t('register')}</Link>
            )}
          </Typography>
        </Box>
      </Toolbar>
    </AppBar>
  );
}
