import { Box, Button, Card, Typography } from '@mui/material';
import Loading from 'modules/shared/components/loading/loading-spinner.component';
import { AlertFeedback } from 'modules/shared/feedback/alert-feedback';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

export default function WaitingForUpdate(props: {
  checkForDataChange: () => Promise<boolean>;
  isActionInOtherWindowRequired?: boolean;
}) {
  const [checkLoading, setCheckLoading] = useState(false);
  const { t } = useTranslation();

  const checkForUpdate = async () => {
    setCheckLoading(true);
    try {
      const result = await props.checkForDataChange();
      if (!result) {
        if (props.isActionInOtherWindowRequired) {
          AlertFeedback.info.actionInOtherWindowRequired();
        } else {
          AlertFeedback.info.pleaseWait();
        }
      }
    } catch (error) {
      AlertFeedback.error.defaultError();
    }
    setCheckLoading(false);
  };

  useEffect(() => {
    const handleVisibilityChange = async () => {
      if (!document.hidden) {
        await checkForUpdate();
      }
    };
    document.addEventListener('visibilitychange', handleVisibilityChange);
    return () => {
      document.removeEventListener('visibilitychange', handleVisibilityChange);
    };
  }, []);

  return (
    <Card className="FormCard" elevation={0}>
      <Typography variant="h3">{t('waiting_for_update')}</Typography>
      <Typography variant="subtitle1">
        {props.isActionInOtherWindowRequired
          ? t('take_action_in_other_window')
          : t('processing_data')}
      </Typography>
      <Box marginTop="1rem" display="flex" justifyContent="space-between" alignItems="center">
        <Loading loading />
        <Button disabled={checkLoading} onClick={checkForUpdate}>
          {t('reload_manual')}
        </Button>
      </Box>
    </Card>
  );
}
