import React, { useContext } from 'react';
import { Button } from '@mui/material';
import { FormikContext } from 'formik';

type FormSubmitButtonProps = {
  label: string;
  size?: 'small' | 'large' | 'medium';
  disabled?: boolean;
  color?: 'inherit' | 'primary' | 'secondary' | 'success' | 'error' | 'info' | 'warning';
};

/**
 * Submit button within formik context.
 * @param props
 * @returns
 */
export default function FormSubmitButton(props: FormSubmitButtonProps) {
  const { dirty, isSubmitting, isValid, isValidating } = useContext(FormikContext);
  const disabled = [props.disabled, !dirty, isSubmitting, !isValid, isValidating].some(Boolean);

  return (
    <Button
      size={props.size || 'large'}
      variant="contained"
      type="submit"
      disabled={disabled}
      color={props.color || 'primary'}
    >
      {props.label}
    </Button>
  );
}
