import CheckCircleOutlineTwoToneIcon from '@mui/icons-material/CheckCircleOutlineTwoTone';
import ErrorOutlineTwoToneIcon from '@mui/icons-material/ErrorOutlineTwoTone';
import { Box, Typography } from '@mui/material';
import MailApi from 'modules/shared/api/mail.api';
import Loading from 'modules/shared/components/loading/loading-spinner.component';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useSearchParams } from 'react-router-dom';

export default function ConsentGooglePage() {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const code = searchParams.get('code');

  const [loading, setLoading] = useState(true);
  const [isError, setIsError] = useState(false);

  useEffect(() => {
    async function getGoogleCallback() {
      if (code) {
        try {
          await MailApi.GetGoogleApiCallback(code);
        } catch (error) {
          setIsError(true);
        }
        setLoading(false);
      }
    }
    getGoogleCallback();
  }, []);

  if (loading) {
    return (
      <>
        <Box marginBottom="1rem">
          <Loading loading />
        </Box>
        <Typography variant="h3">{t('processing_data')}</Typography>
      </>
    );
  }
  if (isError) {
    return (
      <>
        <Box marginBottom="1rem">
          <ErrorOutlineTwoToneIcon color="primary" sx={{ height: '48px', width: '48px' }} />
        </Box>
        <Typography variant="h3" mb="1rem">
          {t('error_default')}
        </Typography>
        <Typography variant="h6">{t('close_window_try_again')}</Typography>
      </>
    );
  }
  return (
    <>
      <Box marginBottom="1rem">
        <CheckCircleOutlineTwoToneIcon color="success" sx={{ height: '48px', width: '48px' }} />
      </Box>
      <Typography variant="h3" mb="1rem">
        {t('gmail_setup_success')}
      </Typography>
      <Typography variant="h6">{t('close_window')}</Typography>
    </>
  );
}
