import {
  Box,
  CircularProgress,
  Container,
  Slide,
  StyledEngineProvider,
  ThemeProvider,
} from '@mui/material';
import theme from 'material/theme';
import LightHeader from 'modules/app/components/light-header/light-header.component';
import ErrorPage from 'modules/app/pages/error/error.page';
import NotFoundPage from 'modules/app/pages/not-found/not-found.page';
import RootPage from 'modules/app/pages/root/root.page';
import useAuthentication from 'modules/authentication/hooks/use-authentication.hook';
import ForgotPage from 'modules/authentication/pages/forgot/forgot.page';
import LoginPage from 'modules/authentication/pages/login/login.page';
import PackagesPage from 'modules/authentication/pages/packages/packages.page';
import PaymentPage from 'modules/authentication/pages/payment/payment.page';
import RegisterPage from 'modules/authentication/pages/register/register.page';
import AuthenticationContext from 'modules/authentication/state/authentication.context';
import BillingPage from 'modules/misc/pages/billing/billing.page';
import SettingsPage from 'modules/misc/pages/settings/settings.page';
import TypographyPage from 'modules/misc/pages/typography/typography.page';
import 'modules/shared/api/axios/axios-default-config';
import Delay from 'modules/shared/components/delay/delay.component';
import Version from 'modules/shared/components/version/version.component';
import UserContext from 'modules/shared/user/state/user.context';
import { SnackbarProvider } from 'notistack';
import React from 'react';
import { BrowserRouter, Outlet, Route, Routes } from 'react-router-dom';
import './App.css';
import ClientsAddPage from 'modules/clients/pages/clients-add/clients-add.page';
import EmailPage from 'modules/settings/email/email.page';
import ConsentGooglePage from 'modules/misc/pages/consent/google/consent-google.page';
import ConsentPage from 'modules/misc/pages/consent/consent.page';
import CasesList from 'modules/cases/cases-list.component';
import CaseStatusPage from 'modules/settings/case-status/case-status.page';

export default function App() {
  const { apiKey, email, user, onLogin, onLogout, validating } = useAuthentication();

  return (
    <BrowserRouter>
      <SnackbarProvider
        maxSnack={3}
        preventDuplicate
        TransitionComponent={Slide}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
      />
      <StyledEngineProvider injectFirst>
        <ThemeProvider theme={theme}>
          {validating ? (
            <div className="Page">
              <Delay>
                <div className="App-LoadingWrapper">
                  <CircularProgress color="primary" size={32} />
                </div>
              </Delay>
            </div>
          ) : (
            <AuthenticationContext.Provider
              value={{ apiKey, email, onLogin, onLogout, validating }}
            >
              {!user && (
                <div className="Page">
                  <LightHeader />
                  <div className="Page-Content">
                    <Box position="absolute" bottom=".5rem">
                      <Version />
                    </Box>
                    <Container className="Authentication-Container">
                      <Routes>
                        <Route element={<LoginPage />} index />
                        <Route element={<RegisterPage />} path="/register">
                          <Route element={<PackagesPage />} index />
                          <Route element={<PaymentPage />} path="payment" />
                        </Route>
                        <Route element={<ForgotPage />} path="/forgot" />

                        <Route path="*" element={<NotFoundPage />} />
                      </Routes>
                    </Container>
                  </div>
                </div>
              )}
              {user && (
                <UserContext.Provider value={user}>
                  {/* Logged in user stuff */}
                  <Routes>
                    <Route path="/consentscreen" element={<ConsentPage />}>
                      <Route path="google" element={<ConsentGooglePage />} />
                    </Route>
                    <Route element={<RootPage />} path="/" errorElement={<ErrorPage />}>
                      <Route
                        index
                        element={
                          <Container>
                            <CasesList />
                          </Container>
                        }
                      />
                      <Route path="clients-add" element={<ClientsAddPage />} />
                      <Route path="email" element={<EmailPage />} />
                      <Route path="status" element={<CaseStatusPage />} />
                      <Route path="settings" element={<SettingsPage />}>
                        <Route index element={<div>GENERAL</div>} />
                        <Route path="billing" element={<BillingPage />} />
                        <Route path="password" element={<div>PASSWORD</div>} />
                        <Route path="*" element={<div>Im the Settings Fallback</div>} />
                      </Route>
                      <Route path="typography" element={<TypographyPage />} />
                      <Route path="*" element={<div>Im the Fallback</div>} />
                    </Route>
                  </Routes>
                </UserContext.Provider>
              )}
            </AuthenticationContext.Provider>
          )}
        </ThemeProvider>
      </StyledEngineProvider>
    </BrowserRouter>
  );
}
