import { Form, Formik } from 'formik';
import FormSubmitButton from 'modules/shared/components/form-submit-button/form-submit-button.component';
import FormTextField from 'modules/shared/components/form-text-field/form-text-field.component';
import React from 'react';
import { Link } from 'react-router-dom';
import './forgot.page.css';
import AuthenticationApi from 'modules/authentication/api/authentication.api';
import { useTranslation } from 'react-i18next';
import { Box, Card, CardHeader, Typography } from '@mui/material';
import validateForm from './helper/validate-form/validate-form.helper';

export type FormValues = {
  email: string;
};

export default function ForgotPage() {
  const { t } = useTranslation();
  return (
    <>
      <Box my="1em">
        <Typography variant="h3" component="h1" textAlign="center">
          {t('reset_password')}
        </Typography>
      </Box>
      <Box my="1em">
        <Typography variant="body1" component="h4" textAlign="center" mb={3}>
          <span>{t('forgot_password_subtitle')} </span>
          <Link to="/">{t('back_to_login')}</Link>
        </Typography>
      </Box>

      <Box display="flex" justifyContent="center">
        <Card className="FormCard">
          <CardHeader title={t('your_account')} />
          <Formik<FormValues>
            initialValues={{
              email: '',
            }}
            onSubmit={async (values) => {
              AuthenticationApi.ResetPassword(values.email);
            }}
            validate={validateForm}
          >
            <Form className="Forgot-Form">
              <FormTextField
                id="email"
                name="email"
                label={t('email')}
                type="email"
                fullWidth
                focus
              />

              <div className="Forgot-ActionContainer">
                <FormSubmitButton label={t('send_email')} />
              </div>
            </Form>
          </Formik>
        </Card>
      </Box>
    </>
  );
}
