import CustomerApi, { ClientData, Customer } from 'modules/shared/api/customer.api';
import { AlertFeedback } from 'modules/shared/feedback/alert-feedback';
import React, { useEffect, useState } from 'react';

/**
 * Context to store all customer related data.
 */
export type CustomerContextType = {
  customer?: {
    name: string;
    id: number;
  };
  clients: ClientData[];
  clientFolders: ClientFolderType[];
  addClient: (client: ClientData) => void;
};

export type ClientFolderType = {
  name: string;
  id: number;
  protected: boolean;
  sort: number;
};

const CustomerContext = React.createContext<CustomerContextType>({
  customer: undefined,
  clients: [],
  clientFolders: [],
  addClient: () => undefined,
});
CustomerContext.displayName = 'authentication-context';

export function useCustomerContext(): CustomerContextType {
  const [clientList, setClientList] = useState<ClientData[]>([]);
  const [customer, setCustomer] = useState<Customer>();
  const [clientFolders, setClientFolders] = useState<ClientFolderType[]>([]);

  useEffect(() => {
    const loadClientList = async () => {
      try {
        const result = await CustomerApi.LoadListOfClients();
        setClientList(result.data.response);
      } catch (error) {
        AlertFeedback.error.defaultError();
      }
    };
    const loadClientFolders = async () => {
      try {
        const result = await CustomerApi.LoadListOfClientFolders();
        setClientFolders(result.data.response);
      } catch (error) {
        AlertFeedback.error.defaultError();
      }
    };
    loadClientFolders();
    loadClientList();
  }, []);

  useEffect(() => {
    const loadCustomer = async () => {
      try {
        const result = await CustomerApi.LoadCustomerOfUser();
        setCustomer(result.data.response);
      } catch (error) {
        AlertFeedback.error.defaultError();
      }
    };
    loadCustomer();
  }, []);

  const addClient = (client: ClientData) => setClientList([...clientList, client]);

  return { clients: clientList, customer, addClient, clientFolders };
}

export default CustomerContext;
