import { Field } from 'formik';
import React from 'react';
import PasswordInput, { PasswordInputProps } from '../password-input/password-input.component';

type FormPasswordInputProps = Omit<PasswordInputProps, 'onChange' | 'value' | 'errors'> & {
  name: string;
  required?: boolean;
};

/**
 * Password input to use in formik context
 * @param props
 * @returns
 */
export default function FormPasswordInput(props: FormPasswordInputProps) {
  return (
    <Field name={props.name}>
      {({ field, form: { errors }, meta }) => (
        <PasswordInput
          {...props}
          value={field.value}
          onChange={(event) => field.onChange(event)}
          error={errors[props.name]}
          touched={meta.touched}
        />
      )}
    </Field>
  );
}
