import { Container, Typography } from '@mui/material';
import axios from 'axios';
import MailApi, { MailConfigType, MailConfiguration } from 'modules/shared/api/mail.api';
import Loading from 'modules/shared/components/loading/loading-spinner.component';
import { AlertFeedback } from 'modules/shared/feedback/alert-feedback';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import ActiveConnectionCard from './components/active-connection-card.component';
import GoogleCard from './components/google-card.component';
import WaitingForUpdate from './components/waiting-for-update.component';
import './email.page.css';

export default function EmailPage() {
  const { t } = useTranslation();
  const [configuration, setConfiguration] = useState<MailConfiguration>();
  const [loading, setLoading] = useState(true);

  const [setupProgressType, setSetupProgressType] = useState<MailConfigType>();

  // Load config to get if something is already setted up
  const loadCustomerMailConfiguration = async () => {
    const result = await axios.get<{ response: MailConfiguration; reference: string }>('/mail');
    setConfiguration(result.data.response);
    if (result.data.response) {
      setSetupProgressType(undefined);
    }
    return !!result.data.response;
  };

  const syncFunction = async (type) => {
    if (type === MailConfigType.GOOGLE) {
      const newConfiguration = await MailApi.SyncGoogleMails();
      console.log(
        ' ~ file: email.page.tsx:33 ~ syncFunction ~ newConfigResult:',
        newConfiguration
      );
      setConfiguration(newConfiguration);
    }
  };

  useEffect(() => {
    async function getInitialConfig() {
      try {
        await loadCustomerMailConfiguration();
        setLoading(false);
      } catch (error) {
        AlertFeedback.error.defaultError();
      }
    }
    getInitialConfig();
  }, []);

  return (
    <Container>
      <Typography variant="h4" component="h1" sx={{ marginBottom: '2rem' }}>
        {t('email_preferences')}
      </Typography>

      {loading && <Loading loading />}

      {!configuration && !loading && (
        <Typography variant="body2" component="p" sx={{ marginBottom: '2rem' }}>
          {t('email_info_subtitle')}
        </Typography>
      )}

      {configuration && (
        <>
          <Typography variant="body2" component="p" sx={{ marginBottom: '2rem' }}>
            {t('email_info_subtitle_done')}
          </Typography>

          <ActiveConnectionCard
            configuration={configuration}
            syncFunction={() => syncFunction(MailConfigType.GOOGLE)}
          />
        </>
      )}

      {setupProgressType === MailConfigType.GOOGLE && (
        <WaitingForUpdate
          checkForDataChange={loadCustomerMailConfiguration}
          isActionInOtherWindowRequired
        />
      )}

      {!configuration && !setupProgressType && !loading && (
        <GoogleCard onSetup={() => setSetupProgressType(MailConfigType.GOOGLE)} />
      )}
    </Container>
  );
}
