import * as SuccessFeedback from './messages/success';
import * as ErrorFeedback from './messages/error';
import * as InfoFeedback from './messages/info';

/**
 * This Object is used to easily show snacks within the snackbar to users.
 * @example
 * AlertFeedback.error.defaultError();
 */
export const AlertFeedback = {
  success: { ...SuccessFeedback },
  error: { ...ErrorFeedback },
  info: { ...InfoFeedback },
};
