import {
  FormControl,
  FormHelperText,
  InputLabel,
  MenuItem,
  Select,
  SelectProps,
} from '@mui/material';
import { Field } from 'formik';
import React from 'react';
import { useTranslation } from 'react-i18next';

export default function FormSelect(
  props: SelectProps & {
    name: string;
    helperText?: string;
    options: { value: number | string; label: string }[];
    MenuItem?: React.FC<{ value: string | number; key: string | number; label: string }>;
  }
) {
  const { t } = useTranslation();
  const { MenuItem: PropsMenuItem } = props;
  const labelId = `${props.id}-label`;
  return (
    <Field name={props.name}>
      {({ field, form: { errors } }) => (
        <FormControl sx={{ my: 1, width: '100%', ...(props.sx || {}) }}>
          <InputLabel id={labelId}>{props.label}</InputLabel>
          <Select
            labelId={labelId}
            id={props.id || props.name}
            name={props.id || props.name}
            onChange={(event) => field.onChange(event)}
            value={field.value || ''}
            label={props.label}
            placeholder={props.placeholder}
          >
            <MenuItem value="">
              <em>{t('please_select')}</em>
            </MenuItem>

            {props.options.map(({ value, label }) => (
              <MenuItem value={value} key={value}>
                {PropsMenuItem ? <PropsMenuItem value={value} key={value} label={label} /> : label}
              </MenuItem>
            ))}
          </Select>
          <FormHelperText>{errors[props.name] || props.helperText || ' '}</FormHelperText>
        </FormControl>
      )}
    </Field>
  );
}
