import axios from 'axios';
import { BillingAddress } from '../pages/billing/components/billing-address-form.component';
import { CreditCardParameters } from '../pages/billing/components/credit-card.component';

/**
 * Update Billing
 * @param values BillingAddress
 */
async function UpdateBillingAddress(values: BillingAddress) {
  await axios.put('/settings/billing', { ...values });
}

/**
 * Load Billing
 */
async function LoadBillingAddress(): Promise<BillingAddress | null> {
  const result = await axios.get('/settings/billing');
  return result?.data?.response;
}
/**
 * Update Credit card
 * @param values
 */
async function UpdateCreditCard(values: CreditCardParameters) {
  await axios.put('/settings/credit-card', { ...values });
}

/**
 * Load credit card data
 */
async function LoadCreditCard(): Promise<CreditCardParameters | null> {
  const result = await axios.get('/settings/credit-card');
  return result?.data?.response;
}

type SettingsApiType = {
  UpdateBillingAddress: typeof UpdateBillingAddress;
  LoadBillingAddress: typeof LoadBillingAddress;
  UpdateCreditCard: typeof UpdateCreditCard;
  LoadCreditCard: typeof LoadCreditCard;
};

const SettingsApi: SettingsApiType = {
  UpdateBillingAddress,
  LoadBillingAddress,
  UpdateCreditCard,
  LoadCreditCard,
};

export default SettingsApi;
