interface Case {
  caseClientId: number;
  clientName: string;
  status: number;
  caseTitle: string;
  lastUpdate: string;
}

function createData(
  clientName: string,
  lastUpdate: string,
  caseClientId: number,
  status: number,
  caseTitle: string
): Case {
  return {
    clientName,
    lastUpdate,
    caseClientId,
    status,
    caseTitle,
  };
}

