import { Autocomplete, Box, TextField, TextFieldProps } from '@mui/material';
import { Field } from 'formik';
import countries from 'modules/shared/constants/countries';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { createFilterOptions } from '@mui/material/Autocomplete';

export default function FormCountrySelect(props: TextFieldProps) {
  const { name, label, fullWidth } = props;
  const { t, i18n } = useTranslation();

  return (
    <Field name={name}>
      {({ field, form: { errors } }) => {
        let inputValue = field.value;
        if (inputValue && i18n.exists(inputValue)) {
          inputValue = t(inputValue);
        }
        return (
          <Autocomplete
            id="country-select-demo"
            options={countries}
            autoHighlight
            autoSelect
            autoComplete
            filterOptions={createFilterOptions({
              stringify: (option) => option.code + t(option.label) + option.phone,
            })}
            fullWidth={fullWidth}
            onChange={(event, value) => {
              field.onChange({ target: { value: value?.label || value || '', id: name } });
            }}
            getOptionLabel={(option) => option.label}
            renderOption={(autoCompleteProps, option) => (
              <Box
                component="li"
                sx={{ '& > img': { mr: 2, flexShrink: 0 } }}
                {...autoCompleteProps}
              >
                <img
                  loading="lazy"
                  width="20"
                  src={`https://flagcdn.com/w20/${option.code.toLowerCase()}.png`}
                  srcSet={`https://flagcdn.com/w40/${option.code.toLowerCase()}.png 2x`}
                  alt=""
                />
                {t(option.label)} ({option.code}) +{option.phone}
              </Box>
            )}
            inputValue={inputValue}
            renderInput={(params) => (
              <TextField
                {...params}
                required={props.required}
                label={label}
                inputProps={{
                  ...params.inputProps,
                  autoComplete: 'new-password', // disable autocomplete and autofill
                }}
                error={errors[name || '']}
                helperText={errors[name || ''] || ' '}
                onChange={(event) => {
                  field.onChange({ target: { value: event.target.value || '', id: name } });
                }}
              />
            )}
          />
        );
      }}
    </Field>
  );
}
