import { Box, Button, Card, CardHeader, Typography } from '@mui/material';
import GmailLogo from 'assets/gmail.svg';
import React from 'react';
import '../email.page.css';
import MailApi from 'modules/shared/api/mail.api';
import { useTranslation } from 'react-i18next';

export default function GoogleCard({ onSetup }: { onSetup: () => void }) {
  const { t } = useTranslation();

  const onClick = () => {
    MailApi.GetGmailSetupUrl();
    onSetup();
  };
  return (
    <Card className="FormCard EmailCard" elevation={0}>
      <Box display="flex" justifyContent="space-between">
        <GmailLogo />
        <Box paddingLeft="1rem" display="flex" flexDirection="column">
          <CardHeader title={t('connect_to_google')} />
          <Typography variant="body2" component="p" sx={{ marginBottom: '1rem' }}>
            {t('email_gmail_card_info')}
          </Typography>
          <Button variant="contained" sx={{ alignSelf: 'end' }} onClick={onClick}>
            {t('setup_gmail')}
          </Button>
        </Box>
      </Box>
    </Card>
  );
}
